import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export const constantRouterMap = [
  {
    path: '/',
    redirect: {
      name: 'login'
    }
  },
  {
    path: '/selfLiftingOrder',
    name: 'selfLiftingOrder',
    component: () =>
      import('@/views/selfLiftingOrder'),
    hidden: true
  },
  {
    path: '/outGoingOrder',
    name: 'outGoingOrder',
    component: () =>
      import('@/views/outGoingOrder'),
    hidden: true
  },
  {
    path: '/goodsManage',
    name: 'goodsManage',
    component: () =>
      import('@/views/goodsManage'),
    hidden: true
  },
  {
    path: '/goodsEdit',
    name: 'goodsEdit',
    component: () =>
      import('@/views/goodsEdit'),
    hidden: true
  },
  {
    path: '/setting',
    name: 'setting',
    component: () =>
      import('@/views/setting'),
    hidden: true
  },
  {
    path: '/storeManage',
    name: 'storeManage',
    component: () =>
      import('@/views/storeManage'),
    hidden: true
  },
  {
    path: '/editDoTime',
    name: 'editDoTime',
    component: () =>
      import('@/views/editDoTime'),
    hidden: true
  },
  {
    path: '/editTel',
    name: 'editTel',
    component: () =>
      import('@/views/editTel'),
    hidden: true
  },
  {
    path: '/accountSafe',
    name: 'accountSafe',
    component: () =>
      import('@/views/accountSafe'),
    hidden: true
  },
  {
    path: '/changePsd',
    name: 'changePsd',
    component: () =>
      import('@/views/changePsd'),
    hidden: true
  },
  {
    path: '/printerSetting',
    name: 'printerSetting',
    component: () =>
      import('@/views/printerSetting'),
    hidden: true
  },
  {
    path: '/login',
    name: 'login',
    component: () =>
      import('@/views/login'),
    hidden: true
  }
]

export default new Router({
  mode: 'history',
  scrollBehavior: () => ({
    y: 0
  }),
  routes: constantRouterMap
})



