import Vue from 'vue'
import router from './router'
import '@/style/index.styl'
import '@/permission'
import App from './App'
import {
  /* eslint-disable no-unused-vars */
  Style,
  Button,
  Toast,
  Picker,
  DatePicker,
  Scroll,
  CheckboxGroup,
  Switch,
  Input,
  Dialog
} from 'cube-ui'

Vue.use(Button)
Vue.use(Toast)
Vue.use(Picker)
Vue.use(Scroll)
Vue.use(DatePicker)
Vue.use(CheckboxGroup)
Vue.use(Switch)
Vue.use(Input)
Vue.use(Dialog)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



